:root {
    --clr-primary: #282c34;
    --clr-secondary: antiquewhite;
    --clr-tertiary: #38388f;
    --clr-quaternary: #3a3a3a;
}

html,
body,
ul {
    padding: 0;
    margin: 0;
}

html {
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */

    font-family: "Quicksand", sans-serif;
    background: var(--clr-primary);
    height: 100vh;
}

::-webkit-scrollbar {
    width: 0.5rem;
}

::-webkit-scrollbar-track {
    background: #1e1e24;
}

::-webkit-scrollbar-thumb {
    background: #6649b8;
}

h1 {
    text-decoration: underline;
    color: var(--clr-secondary);
    font-weight: 700;
    position: sticky;
    top: 0;
    z-index: 1;
    background: var(--clr-primary);
    text-align: center;
    padding-block: 10px;
    margin-block: 0;
}

ul {
    list-style: none;
}

form {
    margin-top: 3rem;
    max-width: 728px;
    display: flex;
    font-size: 1.5rem;
    position: sticky;
    bottom: 0;
    z-index: 1;
    background: var(--clr-primary);
}

input {
    line-height: 1;
    width: 100%;
    font-size: 1.5rem;
    background: var(--clr-quaternary);
    color: var(--clr-secondary);
    outline: none;
    border: none;
    padding: 10px;
}

button {
    background-color: var(--clr-tertiary);
    border: none;
    color: var(--clr-secondary);
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    font-size: 1.25rem;
    transition: background-color 0.3s ease, color 0.3s ease;
    font-weight: 700;
}

button:hover {
    background-color: var(--clr-secondary);
    color: var(--clr-quaternary);
}

.container {
    --cols: 1;

    padding-block: 30px;
    max-width: 900px;
    display: grid;
    grid-template-columns: repeat(var(--cols), 1fr);
    align-items: start;
    gap: 40px;
    margin: auto;
}

@media screen and (min-width: 768px) {
    .container {
        --cols: 2;
    }
}

#recipe-container,
#meals-container {
    --container-height: 30vh;

    border: 3px solid var(--clr-secondary);
    border-radius: 20px;
    min-height: 200px;
    min-width: 200px;
    max-height: var(--container-height);
    overflow: auto;
}

@media screen and (min-width: 768px) {
    #recipe-container,
    #meals-container {
        --container-height: 75vh;
    }
}

#meals-container {
    padding-bottom: 100px;
}

#recipe-list,
#meals-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.recipe-item,
.meal-item {
    font-size: clamp(20px, 2.5vw, 26px);
    border: 3px solid var(--clr-quaternary);
    padding: 10px;
    display: flex;
    width: fit-content;
    border-radius: 20px;
    background: var(--clr-tertiary);
    color: var(--clr-secondary);
    transition: opacity 0.2s ease;
    position: relative;
}

.meal-item {
    cursor: pointer;
}

.recipe-item[draggable="true"] {
    cursor: grab;
}

.dragged {
    opacity: 0.4;
}

.drag-over {
    border-style: dashed !important;
}

.hidden {
    display: none;
}
